<template>
  <div id="show-blogs" v-theme:cloumn = " 'wide' " >
  	<h2 >博客总览</h2> 
  	<input type="text" placeholder="搜索" v-model = "search" >
  	<div class="single-blog" v-for = "blog in filteredBlogs" v-bind:key = "blog.title">
      <router-link v-bind:to= "'/blog/'+ blog.id">
		    <h3 v-rainbow>{{ blog.title  }}</h3>
		    <p>{{ blog.content  }}</p>
      </router-link>
  	</div> 
  </div>
</template>

<script>
// 引入axios
import axios from 'axios' 

export default {
  name: 'show-blog',
  data(){
  	return{
  		blogs:[],
  		search:''
  	}
  },
  created:function(){
  	axios.get('https://vue-myblog-f337c.firebaseio.com/posts.json')//请求网络地址
  		.then(function(data){        
        return data.data
  		})
      .then((data) =>{
        var blogArry = [];
        for(let key in data){
           data[key].id = key;
           blogArry.push(data[key])
        }
         this.blogs = blogArry;
      })
  },
  computed:{
  	filteredBlogs:function(){
  		return this.blogs.filter((blog) =>{
  			return blog.title.match(this.search);
  		})
  	}
  }
  // 局部自定义过滤器
  // filters:{
  // 	//自定义把title都大写//ShowBlog.vue <h3 v-rainbow>{{ blog.title | to-uppercase }}</h3>
  // 	//第一种方法
  // 	// 'to-uppercase':function(value){
  // 	// 	return value.toUpperCase();
  // 	// },
  // 	//第二种方法
  // 	// 'to-uppercase'(value){
  // 	// 	return value.toUpperCase();
  // 	// }
  // }
  
  //局部自定义指令
  // directives:{
  // 	'rainbow':{
  // 		bind(el,binding,vnode){
  // 			el.style.color = "#" + Math.random().toString(16).slice(2,8);
  // 		}
  // 	}
  // }
}

</script>

<style scoped>
#show-blogs{
	max-width: 800px;
	margin:0 auto;

}
.single-blog{
	padding:20px;
	margin:20px 0;
	box-sizing: border-box;
	background: #eee;
}
#show-blogs a{
  color:#444;
  text-decoration: none;
}

input[type="text"]{
  padding:8px;
  width:100%;
  box-sizing: border-box;
}

h2{color:#fff;}


</style>
