import Vue from 'vue'
import App from './App.vue'


//引入路由
import VueRouter from 'vue-router'

import Routes from './routes'

//引入vueResource
// import VueResource from 'vue-resource'

// // 引入axios
// import axios from 'axios' 
//全局配置
// axios.default.baseURl = 'https://vue-myblog-f337c.firebaseio.com'



Vue.config.productionTip = false

//使用路由
Vue.use(VueRouter)
// 使用vueresource
// Vue.use(VueResource)

//自定义指令//title变成随机彩色
Vue.directive(
	'rainbow',{
		bind(el,binding,vnode){
			el.style.color = "#" + Math.random().toString(16).slice(2,8) ;
		}
	}
);

//全局自定义指令//宽度
Vue.directive(
	'theme',{
		bind(el,binding,vnode){
			if(binding.value =='wide'){
				el.style.maxWidth = "960px"
			}	else if(binding.value =='narrow'){
				el.style.maxWidth = "560px"
			}

			// <div id="show-bolgs" v-theme:cloumn = " 'wide' " >
			if(binding.arg == 'cloumn'){
				el.style.background="#6677cc"
				el.style.padding = "20px"
			}

		}
	}
)
//全局自定义过滤器 
//自定义把title都大写//ShowBlog.vue <h3 v-rainbow>{{ blog.title | to-uppercase }}</h3>
// Vue.filter('to-uppercase',function(value){
// 	return value.toUpperCase();
// }); 
//自定义把内容缩小到只显示十个字符//ShowBlog.vue <p>{{ blog.body | snippet }}</p>
// Vue.filter('snippet',function(value){
// 	return value.slice(0,10) + '...';
// });

//创建路由
const router = new VueRouter({
	routes:Routes,
	mode:'history'
})


new Vue({
  render: h => h(App),
  router:router
}).$mount('#app')
