<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if = "!submmited">
      <label>博客标题</label>
      <input type="text" required="required"  v-model = "blog.title"/>
      <label>博客内容</label>
      <textarea v-model = "blog.content"></textarea>
      <div id="checkbox">
        <label>vue.js</label>
        <input type="checkbox" value="vue.js" v-model = "blog.jobs"  >
        <label>react.js</label>
        <input type="checkbox" value="react.js" v-model = "blog.jobs" >
        <label>node.js</label>
        <input type="checkbox" value="node.js" v-model = "blog.jobs" >
        <label>angular.js</label>
        <input type="checkbox" value="angular.js" v-model = "blog.jobs" >
      </div>
      <label>作者</label>
      <select v-model = "blog.writer">
        <option  v-for = "writer in writers" v-bind:key = "writer">{{ writer }}</option>
      </select>

      <button v-on:click.prevent = "post()">添加博客</button>
    </form>
    
    <div v-if = "submmited">
      <h3>您的博客发送成功</h3>
    </div>
    <div id="preview">
      <h2>博客总览</h2>
      <p>博客标题</p>
      <p>{{ blog.title }}</p>
      <p>博客内容</p>
      <p>{{ blog.content }}</p>
      <p>博客分类</p>
      <ul >
        <li v-for = "job in blog.jobs" v-bind:key = "job">{{ job }}</li>
      </ul>
      <p>作者:{{ blog.writer }}</p>
    </div>


  </div>
</template>

<script>
// 引入axios
import axios from 'axios' 

export default {
  name: 'add-blog',
  data(){
    return{
      blog:{
        title:'',
        content:'',
        jobs:[],
        writer:''
        
      },
      writers:[
        '冯硕','是我','还是我'
        ],
      submmited:false//这里是提交成功时候添加博客内容就隐藏
    }
  },
  methods:{
    // JSONPlaceholder - 免费的在线REST服务（提供测试用的HTTP请求假数据）
    // http://jsonplaceholder.typicode.com/
    // post:function(){
    //   this.$http.post('http://jsonplaceholder.typicode.com/users',{
    //     title:this.blog.title,
    //     body:this.blog.content,
    //     userId:1
    //   })
    
      //vue-resource
      // post:function(){
      // this.$http.post('https://vue-myblog-f337c.firebaseio.com/posts.json',this.blog)
      //   .then(function(data){
      //     console.log(data);
      //     this.submmited = true;
      //   });
      // }
      
      //axios
      post:function(){
      axios.post('https://vue-myblog-f337c.firebaseio.com/posts.json',this.blog)
        .then((data) =>{
          // console.log(data);
          this.submmited = true;
        });
      }



  },
  props: {
    msg: String
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    maring:20px auto;
    max-width: 600px;
    padding:20px;
  }
  label{
    display: block;
    margin:20px 0 10px;
  }
  input[type="text"],textarea,select{
    display: block;
    width:100%;
    padding:8px;
  }
  
  textarea{
    height: 200px;
  }

  #checkbox label{
    display: inline-block;
    margin-top:0;
  }
  #checkbox input{
    display: inline-block;
    margin-right:10px;
  }

  button{
    display: block;
    margin:20px 0;
    background: crimson;
    color: #fff;
    border:0;
    padding:14px;
    border-radius: 4px;

    font-size:18px;
    cursor: pointer;
  }
  #preview{
    padding:10px 20px;
    border:1px dotted #ccc;
    margin:30px 0;

  }






</style>
