<template>
	<div id="single-blog">
		<h2>{{ blog.title }}</h2>
		<article>{{ blog.content }}</article>
		<p>作者: {{ blog.writer }}</p>
		<p>分类:</p>
		<ul>
			<li v-for = "job in blog.jobs" v-bind:key="job">
				{{ job }}
			</li>
		</ul>
		<button v-on:click = "deleteSingleBlog()">删除</button>
		<router-link v-bind:to =" '/edit/' +id ">编辑</router-link>
	</div>
</template>

<script>
// 引入axios
import axios from 'axios' 

	export default{
		name:'single-blog',
		data(){
			return{
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			axios.get('https://vue-myblog-f337c.firebaseio.com/posts/' + this.id +'.json')
				.then((data) =>{
					this.blog = data.data;
				})
		},
		methods:{
			deleteSingleBlog(){
				axios.delete('https://vue-myblog-f337c.firebaseio.com/posts/' + this.id +'.json')
					.then(response =>{
						this.$router.push({path:'/'})
					})
			}
			
		}

	}
</script>

<style>
	#single-blog{
		max-width: 960px;
		margin:0 auto;
		padding:20px;
		background: #eee;
		border:1px dotted #aaa;
	}
</style>